<template>
    <div>
        <div>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div>
            <sc-search>
                <el-row :gutter="2">
                    <el-col :span="2">
                        <el-button @click="search" size="mini"></el-button>
                    </el-col>
                    <el-col :span="2">
                        <el-button @click="add" size="mini"></el-button>
                    </el-col>
                </el-row>
            </sc-search>
        </div>
        <div style="margin-top: 20px;">
            <div>
                <el-table :data="list" border stripe size="small">
                    <el-table-column prop="id" label="Id"></el-table-column>
                    <el-table-column prop="name" label=""></el-table-column>
                    <el-table-column prop="desc" label=""></el-table-column>
                    <el-table-column prop="sleepTime" label="ms"></el-table-column>
                    <el-table-column prop="retryTimes" label=""></el-table-column>
                    <el-table-column label="">
                        <template slot-scope="scope">{{statusEmun[scope.row.status]}}</template>
                    </el-table-column>
                    <el-table-column label="" width="345px">
                        <template slot-scope="scope">
                            <el-button v-if="scope.row.status != 1" size="mini" type="success" @click="start(scope.row)"></el-button>
                            <el-button v-if="scope.row.status == 1" size="mini" type="warning" @click="stop(scope.row)"></el-button>
                            <el-button size="mini" type="primary" @click="getSpiderResult(scope.row)"></el-button>
                            <el-button size="mini" type="primary" @click="clearSpiderResult(scope.row)"></el-button>
                            <el-button size="mini" type="info" @click="edit(scope.row)"></el-button>
                            <el-button size="mini" type="danger"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <el-dialog title="" :visible.sync="dialogFormVisible">
                <el-form :model="dataItem" label-width="80px" size="mini" label-suffix=":">
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="">
                                <el-input v-model="dataItem.name"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="">
                                <el-input v-model="dataItem.desc"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="">
                                <el-input type="number" v-model="dataItem.retryTimes"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="ms">
                                <el-input type="number" v-model="dataItem.sleepTime"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="22">
                            <el-form-item label="">
                                <el-input v-model="dataItem.startUrl"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="22">
                            <el-form-item label="">
                                <el-input :title="dataItem.userAgent" v-model="dataItem.userAgent"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-form-item label="">
                        <el-row>
                            <el-col :offset="21" :span="2">
                                <el-button size="mini" type="primary" circle icon="el-icon-plus" @click="addRegexTargetUrl"></el-button>
                            </el-col>
                        </el-row>
                    </el-form-item>
                    <el-form-item label>
                        <el-row class="form-row" v-for="(item,index) in dataItem.regexTargetUrls" :key="index" v-model="dataItem.regexTargetUrls[index]" type="flex" justify="left" align="middle" :gutter="10">
                            <el-col :span="22">
                                <el-input v-model="dataItem.regexTargetUrls[index]"></el-input>
                            </el-col>
                            <el-col :span="1">
                                <el-button icon="el-icon-minus" size="mini" circle @click="deleteTargetUrl(index)"></el-button>
                            </el-col>
                        </el-row>
                    </el-form-item>
                    <el-form-item label="">
                        <el-row>
                            <el-col :offset="21" :span="2">
                                <el-button size="mini" type="primary" circle icon="el-icon-plus" @click="addExtractField"></el-button>
                            </el-col>
                        </el-row>
                    </el-form-item>
                    <el-form-item label>
                        <el-row class="form-row" v-for="(value, index) in dataItem.extractFields" :key="index" type="flex" justify="left" align="middle" :gutter="10">
                            <el-col :span="4">
                                <el-input v-model="dataItem.extractFields[index].field"></el-input>
                            </el-col>
                            <el-col :span="1" class="line">-</el-col>
                            <el-col :span="17">
                                <el-input v-model="dataItem.extractFields[index].rule"></el-input>
                            </el-col>
                            <el-col :span="1">
                                <el-button icon="el-icon-minus" size="mini" circle @click="deleteExtractField(index)"></el-button>
                            </el-col>
                        </el-row>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button size="mini" @click="dialogFormVisible = false"> </el-button>
                    <el-button size="mini" type="primary" @click="save"> </el-button>
                </div>
            </el-dialog>
            <el-dialog title="" :visible.sync="dialogTableVisible">
                <el-table :data="resultList" border stripe size="small">
                    <el-table-column v-for="(item,index) in resultColumn" :key="index" :prop="item" :label="item" show-overflow-tooltip="true">
                    </el-table-column>
                    <el-table-column prop="_createTime" label="" width="150">
                        <template slot-scope="scope">
                            <el-popover placement="top-start" :title="scope.row._config_name" width="200" trigger="hover" :content="scope.row._request_url">
                                <span slot="reference">{{scope.row._createTime}}</span>
                            </el-popover>
                        </template>
                    </el-table-column>
                </el-table>
            </el-dialog>
        </div>
    </div>
</template>
<script>
import MockData from "../mock/index.js";
MockData.bootstrap();
export default {
    data() {
        return {
            dialogFormVisible: false,
            dialogTableVisible: false,
            list: [],
            resultColumn: [],
            resultList: [],
            emptyDataItem: {
                name: "",
                desc: "",
                startUrl: "",
                userAgent: "",
                sleepTime: "",
                retryTimes: "",
                regexTargetUrls: [],
                extractFields: []
            },
            dataItem: {},
            statusEmun: { 0: "", 1: "", 2: "" }
        };
    },
    created: function() {
        var that = this;
        that.search();
    },
    methods: {
        add: function() {
            var that = this;
            that.dataItem = JSON.parse(JSON.stringify(that.emptyDataItem));
            that.dialogFormVisible = true;
        },
        edit: function(row) {
            var that = this;
            //,
            //that.dataItem = Object.assign({}, row);
            that.dataItem = JSON.parse(JSON.stringify(row));
            that.dialogFormVisible = true;
        },
        addRegexTargetUrl: function() {
            var that = this;
            that.dataItem.regexTargetUrls.push("");
        },
        addExtractField: function() {
            var that = this;
            that.dataItem.extractFields.push({ field: "", rule: "" });
        },
        deleteTargetUrl: function(index) {
            var that = this;
            that.dataItem.regexTargetUrls.splice(index, 1);
        },
        deleteExtractField: function(index) {
            var that = this;
            that.dataItem.extractFields.splice(index, 1);
        },
        start: function(row) {
            var that = this;
            this.$ajax
                .get(this.$baseUrl + "/job/startSpider?key=" + row.key)
                .then(function(response) {
                    if (response.data.code == 1000) {
                        that.$message.success("");
                        that.dialogFormVisible = false;
                        row.status = response.data.result;
                    } else {
                        that.$message(response.data.message);
                    }
                })
                .catch(function(err) {
                    window.console.log(err);
                });
        },
        stop: function(row) {
            var that = this;
            this.$ajax
                .get(this.$baseUrl + "/job/stopSpider?key=" + row.key)
                .then(function(response) {
                    if (response.data.code == 1000) {
                        that.$message.success("");
                        that.dialogFormVisible = false;
                        row.status = response.data.result;
                    } else {
                        that.$message(response.data.message);
                    }
                })
                .catch(function(err) {
                    window.console.log(err);
                });
        },
        save: function() {
            var that = this;
            this.$ajax
                .post(this.$baseUrl + "/job/saveSpiderConfig", that.dataItem)
                .then(function(response) {
                    if (response.data.code == 1000) {
                        that.$message.success("");
                        that.dialogFormVisible = false;
                    } else {
                        that.$message(response.data.message);
                    }
                })
                .catch(function(err) {
                    window.console.log(err);
                });
        },
        getSpiderResult: function(row) {
            var that = this;
            this.$ajax
                .get(this.$baseUrl + "/job/getSpiderResultList", {
                    params: { key: row.key, page: 0, size: 10 }
                })
                .then(function(response) {
                    if (response.data.code == 1000) {
                        that.dialogTableVisible = true;
                        that.resultColumn = response.data.result.column;
                        that.resultList = response.data.result.list;
                    } else {
                        that.$message(response.data.message);
                    }
                })
                .catch(function(err) {
                    window.console.log(err);
                });
        },
        clearSpiderResult: function(row) {
            var that = this;
            this.$ajax
                .get(this.$baseUrl + "/job/clearSpiderResult", {
                    params: { key: row.key}
                })
                .then(function(response) {
                    if (response.data.code == 1000) {
                        that.$message.success("");
                    } else {
                        that.$message(response.data.message);
                    }
                })
                .catch(function(err) {
                    window.console.log(err);
                });
        },
        search: function() {
            var that = this;
            this.$ajax
                .get(this.$baseUrl + "/job/getSpiderList")
                .then(function(response) {
                    that.list = response.data.result;
                })
                .catch(function(err) {
                    window.console.log(err);
                });
        }
    }
};
</script>
<style>
.line {
    text-align: center;
}
.form-row {
    margin-bottom: 10px;
}
</style>

